@import url(https://cdnjs.cloudflare.com/ajax/libs/lightbox2/2.11.1/css/lightbox.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
	font-family: "ABeeZee";
	src: url(/cs185-project-react/static/media/ABeeZee-Regular.c6d6bab0.ttf);
}

.page-container {
	font-family: ABeeZee, Arial;
	margin: 1em;
	position: relative;
	min-height: 96vh;
}

.header {
	display: flex;
	justify-content: space-between;
	margin-left: 3em;
}

.home {
	font-size: 24px;
	text-decoration: none;
	color: black;
	cursor: pointer;
}

.menu {
	display: flex;
	margin-right: 3em;
	clear: both;
}

.menu-text {
	float: right;
	margin: 1em;
	text-decoration: none;
	color: black;
	font-size: 20px;
	cursor: pointer;
}

.link-text {
	float: right;
	margin: 1em;
	text-decoration: none;
	color: black;
}

.body {
	display: flex;
}

.body > div {
	margin-left: 10em;
}

.body-index {
	display: flex;
	margin-top: 10em;
}

.body-index > div {
	margin: 1em;
}

.home-body {
	font-size: 55px;
}

.project-container {
	display: flex;
	margin: 3em;
	margin-bottom: 5em;
}

.project-container > img {
	width: 300px;
	height: auto;
}

.project-container > div {
	margin-left: 3em;
}

.project-container > div > p {
	font-size: large;
}

.project-img {
	width: 128px;
	height: auto;
}

.images {
	margin: 3em;
}

.image {
	height: 300px;
	width: auto;
	object-fit: cover;
	margin: 0.5em;
	cursor: pointer;
}

.back-to-top {
	position: fixed;
	bottom: 20px;
	right: 30px;
	z-index: 99;
	cursor: pointer;
}

.back-to-top > img {
	width: 64px;
	height: auto;
}

.back-to-top:hover > img {
	-webkit-filter: invert(95%);
	        filter: invert(95%);
}

.videos {
	margin: 3em;
}

.video {
	object-fit: cover;
	margin: 0.5em;
}

.footer {
	display: flex;
	justify-content: space-between;
	position: absolute;
	bottom: 0;
	width: 96%;
	height: 2.5rem;
}

.footer-menu {
	display: flex;
}
